<template>
	<div class="login_box">
		<header>
			<Headnav head_left="" head_title='搜索地址' head_right=" ">
				<router-link to="/shopSite" slot="left" tag="span">
					<van-icon name="arrow-left" />
				</router-link>
			</Headnav>
		</header>
		<div class="login_cont">
			<div class="login_search">
				<input v-model="searchName" type="text" placeholder="  输入学校、商务楼、地址">
				<br>
				<button @click="getData()">搜索</button>
			</div>
			<div class="login_Search">
				<ul>
					<li @click="setName(item)" v-for="(item,k) in list" :key="k">
						<P class="login_tit">{{item.name}}</P>
						<P>{{item.address}}</P>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import Headnav from '../About/headNav'
	export default {
		name: "searchLogin",
		data(){
			return{
				searchName:"",
				indexId:'',
				list:[],
			}
		},
		methods:{
			getData(){
					this.$axios.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.indexId}&keyword="${this.searchName}"`).then(res =>{
						this.list=res.data
				})
			},
			//点击传值到shopSite
			setName(item){
				this.$store.commit('addName',item)
				this.$router.push({path:'/newSearch'})
			},
		},
		mounted(){
			this.indexId =JSON.parse(localStorage.getItem('Id'))
		},
		components:{
			Headnav
		}
	}
</script>

